<template>
  <v-app>
    <v-content>
      <v-container>
        <v-card>
          <v-card-title>车辆模拟</v-card-title>
          <v-card-text>
            <v-text-field v-model="num" label="请输入车牌号"></v-text-field>
          </v-card-text>
          <v-card-actions>
            <v-layout justify-end>
              <v-btn color="primary" @click="submit">启动</v-btn>
            </v-layout>
          </v-card-actions>
        </v-card>
        <ul>
          <li v-for="(item,i) in fileList" :key="i">{{item}}</li>
        </ul>
        <img :src="`/static/${num}`" class="mt-5">
      </v-container>
    </v-content>
  </v-app>

</template>

<script>
import axios from 'axios'
export default {
  name: 'app',
  data() {
    return {
      num: "",
      fileList: []
    }
  },
  async mounted() {
    const fileList = await axios.get('/')
  },
  methods: {
    async submit() {
      await axios.get('/' + this.num.toString())
    }
  },
}
</script>

